import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import "./header.css"

const Header = ({ linksData,selectedItem }) => (
  <header>
  
  {/* googleAnalytics Scripts */}
  <script async src="https://www.googletagmanager.com/gtag/js?id=UA-164290593-1" />
  <script>
    {`
       window.dataLayer = window.dataLayer || [];
       function gtag(){dataLayer.push(arguments);}
       gtag('js', new Date());
       gtag('config', 'UA-164290593-1');
    `}
  </script>


    <ul class="listContainer" >
    {linksData.map((item, key) => {
      return(
      <li class="listClass" style={(selectedItem === item.url) ? { border: "2px solid black"} : { border: "1px solid black" }}><Link
          to={item.url}
          style={{color: `white`,padding: `0px`,marginTop:`10px`}}
        >
          {item.text}
        </Link></li>
      )
    })}

</ul>
  </header>
)


Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
